<template>
  <div class="text-center" id="app">
    <h1>Vue Translator App</h1>
    <h5>Powered by Vue.js</h5>

    <TranslateForm v-on:formSubmit="translateText"></TranslateForm>
    <TranslateOutput v-text="translatedText"></TranslateOutput>


  </div>
</template>

<script>
import TranslateForm from './components/TranslateForm'
import TranslateOutput from './components/TranslateOutput'

export default {
  name: 'app',
  components: {
    TranslateForm,
    TranslateOutput
  },
  data: function() {
    return {
      translatedText: ''
    }
  },
  methods: {
    translateText: function(text, lang) {
      // alert(text);
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20170912T110819Z.9f964612966502e0.0962a6f793bcb0890bb43297a9dfb06e8fa365d6&lang='+lang+'&text='+text)
      .then((response) => {
        this.translatedText = response.body.text[0];
      });

    }
  }
}
</script>

<style>
body {
  background: #fefefe;

}
</style>



